import { Component, OnInit, Input } from '@angular/core';
import { ClosemenuService } from './../services/closemenu.service';
import { GlobalsService } from './../services/globals.service';
import $ from 'jquery';

@Component({
  selector: 'app-sidemenu',
  templateUrl: './sidemenu.component.html',
  styleUrls: ['./sidemenu.component.css'],
})
export class SidemenuComponent implements OnInit {
  
  @Input() pipetype: string;
  constructor(public clmsrve: ClosemenuService, public globals: GlobalsService) {
     
     
   }
    setFav(){
       this.globals.pipetype = "fav";
       this.clmsrve.closeMenu();
       setTimeout(function(){
         $(".homeBtn").addClass("animateShow");
       },100);
     }

    

  ngOnInit() {
   
  }
}



