import { Injectable } from '@angular/core';
import $ from 'jquery';
@Injectable()
export class ClosemenuService {
  constructor() { }
    closeMenu() {
      $("nav").css("transform","translate3d(-260px, 0px, 0px)");
		  $("nav .close, .fadeScreen").fadeOut("fast");
    }
}



