import { Component, OnInit, Input } from '@angular/core';
import { Walllist } from './../shared/wallpaper';
import { WalllistService } from './../services/wallpaperlist.service';
import { WallpathService } from './../services/wallpath.service';
import { GlobalsService } from './../services/globals.service';
import $ from 'jquery';

declare var FileTransfer,cordova,wallpaper: any;

@Component({
  selector: 'app-wallpaperlist',
  templateUrl: './wallpaperlist.component.html',
  styleUrls: ['./wallpaperlist.component.css'],
  inputs:['pipetype']
})

export class WallpaperlistComponent implements OnInit {
  walllist: Walllist[];
  Imgpath = "";
  thumbpath = "";
  popshow: Boolean = false;  
  wallPopScroll;
  wfolder = this.wallpath.wallfolder;
  wallId: number;
  wallpaperWidth: any;
  favoriteList: Array<number> = [];
  downloadList: Array<number> = [];
  favFlag: Boolean;
  testFile = '/storage/emulated/0/wallpic/thumbs/'+139+'.jpg';
  //testFile = "cdvfile://localhost/persistent/wallpic/thumbs/139.jpg";
  //downloadPerc: number = 0;
  
  /*load wallpaper into the popup*/
  loadWallpaperSrc(wallpaper){
      this.popshow = true;
			this.Imgpath=this.wallpath.path+"wallpapers/"+this.wallpath.os+"/"+this.wallpath.wallfolder+"/"+wallpaper.category + wallpaper.id+".jpg";
      this.thumbpath= this.wallpath.thumbpath+wallpaper.category+wallpaper.id+".jpg";
      this.checkWritePerm();
      //console.log(this.thumbpath);
      switch (this.wallpath.wallfolder) {
        case "large":
        this.wallpaperWidth = "1440";
        break;
        case "medium":
        this.wallpaperWidth = "960";
        break;
        case "xlarge":
        this.wallpaperWidth = "2160";
        break;
        case "xxlarge":
        this.wallpaperWidth = "2880";
        break;
        case "small":
        this.wallpaperWidth = "480";
        break;
      }
      this.wallId = wallpaper.id;
      setTimeout(()=>{
        $("#imgscroller").animate({scrollLeft: $(window).width() / 2}, 800);
        $("#imgwrapper").click(function(){
          $("#wallpopup").toggleClass("hideAll");
        });
        var storedIds = JSON.parse(localStorage.getItem("favorites"));
        if(storedIds.indexOf(this.wallId) > -1){
          $(".favIcon").addClass("on");
        }
      },100)
		}

    closeWallPopup(){
      this.popshow = false;
    }

    /*home button*/ 
    resetPipe(){
      this.globals.pipetype = "";
      setTimeout(function(){
          $(".homeBtn").removeClass("animateShow").addClass("animateHide");
        },100);
    }

    /*set wallpaper as favorite*/
    setFavorite(){
      if (localStorage.getItem("favorites") === null) {
        this.favoriteList.push(this.wallId);
        localStorage.setItem("favorites",  JSON.stringify(this.favoriteList));
        $(".favIcon").addClass("on");
        //alert(localStorage.getItem("favorites")+"first");
      }
      else{
        //alert(localStorage.getItem("favorites")+"set");
        var storedIds = JSON.parse(localStorage.getItem("favorites"));
        if(storedIds.indexOf(this.wallId) > -1){
          var index = storedIds.indexOf(this.wallId);
          storedIds.splice(index, 1);
          localStorage.setItem("favorites",  JSON.stringify(storedIds));
          $(".favIcon").removeClass("on");
          //alert("Removed from favorites");
        }else{
          storedIds.push(this.wallId);
          localStorage.setItem("favorites",  JSON.stringify(storedIds));
          $(".favIcon").addClass("on");
          //alert("Added to the favorites!");
        }
      }    
    }

    updateDownloadList(filename){
      if (localStorage.getItem("downloads") === null) {
        this.downloadList.push(filename);
        localStorage.setItem("downloads", JSON.stringify(this.downloadList));
        console.log("Create list and added to downloads!");
        return true;
      }
      else{
        var downloadedIds = JSON.parse(localStorage.getItem("downloads"));
        if(downloadedIds.indexOf(filename) > -1){
          console.log("Already downloaded")
          return false;
          //alert("Removed from favorites");
        }else{
          downloadedIds.push(filename);
          localStorage.setItem("downloads",  JSON.stringify(downloadedIds));
          console.log("Added to downloads!");
          return true;
        }
      }
    }

    /*check write permission*/
    checkWritePerm(){
      var permissions = cordova.plugins.permissions;
      permissions.hasPermission(permissions.WRITE_EXTERNAL_STORAGE, function( status ){
        function error() {
          console.warn('Write permission is not turned on');
        }
        function success( status ) {
          if( !status.hasPermission ) error();
        }
        if (status.hasPermission) {
          return true;          
        }
        else {
          permissions.requestPermission(permissions.WRITE_EXTERNAL_STORAGE, success, error);          
        }
      });
    }
    initiateWallpaperDownload(){
       if(this.checkWritePerm()){
         this.downloadWallpaper();
       }else{
        window['plugins'].toast.showShortBottom("App doesn't have write access", function(a){console.log('toast success: ' + a)}, function(b){alert('toast error: ' + b)})
       }
    }

    /*download wallpaper*/
    downloadWallpaper= () => {
      var wallDownload = new FileTransfer();
      var thumbDownload = new FileTransfer();
      var uri = encodeURI(this.Imgpath);
      var uri2 = encodeURI(this.thumbpath);
      //var fileURL =  cordova.file.externalApplicationStorageDirectory+'downloads/'+this.wallId+'.jpg';
      var fileURL =  cordova.file.externalRootDirectory+'wallpic/'+this.wallId+'.jpg';
      var fileURL2 =  cordova.file.externalRootDirectory+'wallpic/thumbs/'+this.wallId+'.jpg';
      $("#downProShow").show();
      resetDownPerc();    
      
      wallDownload.download(
          uri, fileURL, entry => {
            $("#downProShow .digits,#downProShow .text").html("");
            $(".progressBar span").width(0);
            $("#downProShow").hide();            
            window['plugins'].toast.showShortBottom('Wallpaper downloaded!')
            this.updateDownloadList(this.wallId);
            console.log("download complete: " + entry.toURL());
          },        
          error => {
            console.log("download error source " + error.source);
            console.log("download error target " + error.target);
            console.log("download error code" + error.code);
            $("#downProShow .digits,#downProShow .text").html("");
            $(".progressBar span").width(0);
            $("#downProShow").hide(); 
            window['plugins'].toast.showShortBottom('Download error.')  
          },        
          false, {
            headers: {
                "Authorization": "Basic dGVzdHVzZXJuYW1lOnRlc3RwYXNzd29yZA=="
            }
          }
      );
      thumbDownload.download(
          uri2, fileURL2, function(entry) {
            console.log("Thumbnail download complete: " + entry.toURL());
          },        
          function(error) {
            console.log("Thumb download error source " + error.source);
            console.log("Thumb download error target " + error.target);
            console.log("Thumb download error code" + error.code);
          },        
          false, {
            headers: {
                "Authorization": "Basic dGVzdHVzZXJuYW1lOnRlc3RwYXNzd29yZA=="
            }
          }
      );
      wallDownload.onprogress = (progressEvent) => {
        if (progressEvent.lengthComputable) {
          //this.downloadPerc = Math.floor(progressEvent.loaded / progressEvent.total * 100);          
          var perc = Math.floor(progressEvent.loaded / progressEvent.total * 100);
          $("#downProShow .digits").html(perc);
          $(".progressBar span").width(perc+"%");
          $("#downProShow .text").html("Downloading");
          //console.log(perc);
        } else {
            //this.downloadPerc.increment();
        }
      }
      $("#downProShow .close").click(function(){
        wallDownload.abort();        
        resetDownPerc();  
        setTimeout(function(){$("#downProShow").hide();},200); 
        window['plugins'].toast.showShortBottom('Download canceled.', function(a){console.log('toast success: ' + a)}, function(b){alert('toast error: ' + b)})       
      });
      function resetDownPerc(){
        console.log("reset called");
        $("#downProShow .digits").html(0);
        $(".progressBar span").width(0);
        $("#downProShow .text").html("Starting download");
      }
    }

  constructor( private walllistService: WalllistService, public wallpath: WallpathService, public globals: GlobalsService) {
    
   }
  ngOnInit(){
    this.walllistService.getWalllist().subscribe(walllist => this.walllist = walllist);    
    
  } 
}







