import { Injectable } from '@angular/core';
import $ from 'jquery';
@Injectable()
export class OpenmenuService {
  constructor() { }
  openMenu() {
        $("nav").css("transform","translate3d(0px, 0px, 0px)");
        $("nav .close, .fadeScreen").fadeIn();
    }
}



