import { Pipe, PipeTransform } from '@angular/core';
import { Walllist } from './../shared/wallpaper';

@Pipe({
  name: 'favorites'
})
export class FavoritesPipe implements PipeTransform {
  
  transform(items: Array<any>, item: Array<any>){
        item = items.filter(checkFav);
        return item;
        function checkFav(wallpaper){    
          var storedId = JSON.parse(localStorage.getItem("favorites"));    
          if(storedId.indexOf(wallpaper.id) > -1){
            return wallpaper;
          }
        }
    }

}



