import { Walllist } from './../shared/wallpaper';
import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class WalllistService {

    constructor(private http: Http){ }

    getWalllist(){
        return this.http.get('http://ssgmkharda.in/wallpic/fetchwall.php').map(response => <Walllist[]>response.json().records);
    }
}





