import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';


import { AppComponent } from './app.component';
import { WallpaperlistComponent } from './wallpaperlist/wallpaperlist.component';
import { NavbarComponent } from './navbar/navbar.component';

import { FeaturedPipe } from './shared/wall-featured-filter.pipe';
import { TruncatePipe } from './shared/limit-to.pipe';
import { FavoritesPipe } from './shared/favorites.pipe';
import { MainPipe } from './shared/main.pipe';

import { WalllistService } from './services/wallpaperlist.service';
import { SidemenuComponent } from './sidemenu/sidemenu.component';
import { OpenmenuService } from './services/openmenu.service';
import { ClosemenuService } from './services/closemenu.service';
import { WallpathService } from './services/wallpath.service';
import { GlobalsService } from './services/globals.service';

import { WallpopupComponent } from './wallpopup/wallpopup.component';
import { DownloadOverlayComponent } from './download-overlay/download-overlay.component';




@NgModule({
  declarations: [
    AppComponent,
    WallpaperlistComponent,
    NavbarComponent,
    FeaturedPipe,
    TruncatePipe,
    SidemenuComponent,
    WallpopupComponent,
    FavoritesPipe,
    MainPipe,
    DownloadOverlayComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule
  ],
  providers: [ WalllistService, OpenmenuService, ClosemenuService, WallpathService, GlobalsService ],
  bootstrap: [AppComponent]
})
export class AppModule { }



